:root {
  --color-bg: #{$base00};
  --color-panel: #{$base01};
  --color-border: #{$base03};
  --color-text: #{$base05};
  --color-dim: #{$base04};
  --color-accent: #{$base0d};
  --color-link: #{$base0c};
  --color-warn: #{$base0a};
  --color-hot: #{$base08};
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'JetBrains Mono', ui-monospace, SFMono-Regular, Menlo, Monaco, Consolas, 'Liberation Mono', 'Courier New', monospace;
  background: var(--color-bg);
  color: var(--color-text);
  min-height: 100vh;
  line-height: 1.6;
}

a {
  color: var(--color-link);
  text-decoration: none;
}

a:hover,
a:focus {
  color: var(--color-accent);
}

#galaxy {
  position: fixed;
  inset: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
}

.page {
  position: relative;
  z-index: 1;
  min-height: 100vh;
  padding: 2.5rem 1.25rem 2rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.site-header {
  max-width: 78ch;
  margin: 0 auto;
  width: 100%;
}

.site-title {
  font-size: 1.25rem;
  font-weight: 600;
  color: var(--color-text);
}

.site-tagline {
  color: var(--color-dim);
  margin-top: 0.25rem;
}

.prompt {
  color: var(--color-warn);
}

.cursor {
  display: inline-block;
  width: 0.6ch;
  height: 1.1em;
  margin-left: 0.2ch;
  background: var(--color-text);
  animation: blink 1.2s steps(1, end) infinite;
  vertical-align: text-bottom;
}

@keyframes blink {
  0%, 49% { opacity: 1; }
  50%, 100% { opacity: 0; }
}

.site-nav {
  margin-top: 1rem;
  display: flex;
  flex-wrap: wrap;
  gap: 0.75rem 1.5rem;
}

.nav-link {
  color: var(--color-text);
  background: var(--color-panel);
  padding: 0.25rem 0.5rem;
  border: 1px solid var(--color-border);
}

.nav-link:hover,
.nav-link:focus {
  color: var(--color-accent);
  border-color: var(--color-accent);
}

.content-frame {
  max-width: 78ch;
  margin: 0 auto;
  width: 100%;
  background: rgba(29, 31, 33, 0.9);
  padding: 0;
  border: 1px solid var(--color-border);
  box-shadow: 0 0 32px rgba(0, 0, 0, 0.4);
}

.ascii-line {
  color: var(--color-border);
  white-space: pre;
  text-align: center;
  margin: 0.6rem 0;
}

.ascii-content {
  padding: 0 1rem 1rem;
}

.site-footer {
  max-width: 78ch;
  margin: 0 auto;
  width: 100%;
  color: var(--color-dim);
  font-size: 0.9rem;
}

.page-title {
  margin-top: 0;
  font-size: 1.2rem;
}

.profile-canvas {
  display: block;
  width: 128px;
  height: 128px;
  margin: 0.75rem 0 1rem;
  border: 1px solid var(--color-border);
  image-rendering: pixelated;
  image-rendering: crisp-edges;
}

.post-list {
  list-style: none;
  padding: 0;
  margin: 1rem 0 0;
}

.post-list li {
  margin-bottom: 0.5rem;
}

.post-list li::before {
  content: "* ";
  color: var(--color-warn);
}

.post-date {
  color: var(--color-dim);
  margin-right: 0.5rem;
}

.post-title {
  margin-top: 0;
}

.post-meta {
  color: var(--color-dim);
  margin-bottom: 1.5rem;
}

.post-body h2,
.post-body h3 {
  color: var(--color-accent);
}

.post-body img {
  max-width: 100%;
  height: auto;
  display: block;
}

@media (max-width: 720px) {
  .page {
    padding: 2rem 1rem 1.5rem;
  }

  .ascii-line {
    font-size: 0.8rem;
  }
}
